.navbar {
  text-transform: uppercase;
  letter-spacing: 1px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  height: 10vh;
}

.navbar-brand {
  font-family: "Montserrat", sans-serif !important;
}

.navbar > a {
  font-weight: bold;
  color: #403d52 !important;
  font-family: "Space Mono", monospace;
}

